body {
  font-family: sans-serif;
}
*,
::before,
::after {
  box-sizing: border-box;
}
.logo_wrapper {
  text-align: center;
  padding: 30px 0;
}
.logo {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  display: inline-block;
}
.step_heading {
  font-size: 38px;
  line-height: 44px;
  text-align: center;
  margin-bottom: 40px;
  font-weight: 700;
  color: #1b4e7d;
}
input[type="radio"] ~ label {
  padding: 30px 10px 20px;
  background-color: #fff;
  border-radius: 6px;
  border: 2px solid #eee;
  width: 100%;
  /* max-width: 250px; */
  transition: all 0.15s ease;
  cursor: pointer;
  min-height: 180px;
  padding-top: 10px;
  font-size: 20px;
  font-weight: normal;
  color: #eee;
  display: flex;
  align-items: center;
  justify-content: center;
}
input[type="radio"]:checked ~ label {
  transform: translateY(-3px);
  border: 2px solid #1c4d7f;
  color: #1c4d7f;
  box-shadow: 3px 3px 15px 1px rgb(31 35 60 / 17%);
}
input[type="radio"] ~ label:hover {
  transform: translateY(-3px);
  border: 2px solid #1c4d7f;
  color: #1c4d7f;
  box-shadow: 3px 3px 15px 1px rgb(31 35 60 / 17%);
}
/* input[type="radio"]:checked ~ label,
input[type="radio"] ~ label:hover{

} */
.selection_wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}
.selection_wrap input {
  display: none;
}
.selection_wrap .radio {
  width: 100%;
}
.step_form-wrapper {
  max-width: 800px;
  margin: 0 auto;
}
