.gender-wrap {
  max-width: 550px;
  margin: 0 auto;
}
input[type="text"],
input[type="email"] {
  border-radius: 4px !important;
  height: 50px !important;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  border: 2px solid #eee;
  color: #1c4d7f;
  font-weight: normal;
  text-indent: 20px;
  font-size: 16px;
}
.inputs_wrap {
  margin: 50px auto;
  max-width: 550px;
}
.inputs_wrap-inner {
  margin-bottom: 16px;
}
.next_btn {
  height: 50px;
  font-size: 16px;
  max-width: 550px;
  margin: 0 auto;
  display: inline-block;
  background: #1b4e7d;
}
.inputs_wrap-inner p {
  text-indent: 20px;
  background: #d00000;
  color: #fff;
  padding: 5px;
  border-radius: 4px;
  margin: 0;

  margin-top: 5px;
  font-size: 12px;
}
.form_footer {
  text-align: center;
  margin: 50px 0;
}
.MuiButton-label {
  background: #1b4e7d;
  color: #fff;
  min-width: 150px;
  height: 50px;
  font-size: 16px;
  border-radius: 4px;
  transition: all 0.3s ease;
}
.MuiButton-label:hover {
  text-decoration: none;
  color: #fff;
  transform: translateY(-2px);
}
.text-area-wrap {
  padding: 30px 0;
}

/* range box */
.input-group {
  width: 100%;
  float: left;
  margin-bottom: 30px;
}

.input-group input {
  float: left;
  max-width: 60px;
}

.btn-number {
  width: 100%;
  height: 50px;
  background: #fff;
  border: 1px solid #ccc;
}

.input-group-btn {
  width: 50px;
  height: 50px;
  max-width: 50px;
  display: block;
  float: left;
}

